import React from 'react'
import './App.css';
import { Container, Card, Button, Row, Col, Badge, Body, Title, Overlay } from 'react-bootstrap';
import { IoIosCloud } from 'react-icons/io'
import { ImCloud } from 'react-icons/im'
import Bottom from './Bottom';
import BlueFooter from './Blue';
const Home = () => {
    return (
        <Card className="bg-primary text-white" style={{ height: 700 }}>
            <Card.Body >
                <Row className='Container'>
                    <Col md={6} >
                        <Row >
                            <Col md={8} className='mt-4'>
                                <Card.Title style={{ flex: 1, backgroundColor: 'white', color: 'black', width: 320, height: 35, paddingLeft: 7, paddingTop: 3 }}>Serious Business, Serious Finance</Card.Title>
                            </Col>
                            <Col md={4}>
                                <div style={{ marginLeft:70 }}>
                                    <IoIosCloud color='white' size={75} />
                                </div>
                            </Col>
                        </Row>
                        <Card.Text>
                            <h1 style={{ fontWeight: 'bold', fontFamily: 'sans-serif', fontSize: 50 }}>Simplifing<br />Investing For<br /></h1>
                            <Row>
                                <Col md={2}>
                                    <h1 style={{ fontWeight: 'bold', fontFamily: 'sans-serif', fontSize: 45 }}>You</h1>
                                </Col>
                                <Col md={10}>
                                    <h6 style={{ color: '#b3c6ff', paddingTop: 7, paddingLeft: 2 }}>Timely research and analysis. Intutive <br />yet powerful Business tools.</h6>
                                </Col>
                            </Row>
                            <Card style={{ width:220, height: 45, backgroundColor: '#ff5c33', borderRadius: 5, marginTop: 50}}></Card>
                        </Card.Text>
                        <div style={{ marginLeft:70 }}>
                                    <IoIosCloud color='white' size={75} />
                                </div>
                    </Col>

                    {/*Right Col */}

                    <Col md={6}>
                        <Row>
                            <Col md={8}>
                                <Card style={{ width: 170, height: 170, backgroundColor: '#99bbff', borderRadius: 30, marginTop: 60 }}>
                                    <Card.ImgOverlay>
                                        <Card style={{ width: 170, height: 170, backgroundColor: '#ccd9ff', borderRadius: 30, marginTop: 30, marginLeft: 100 }}>
                                        </Card>
                                    </Card.ImgOverlay>
                                </Card>
                                <div style={{ justifyContent: 'center', alignItems: 'center' ,marginTop:80}}>
                                    <IoIosCloud color='white' size={75} />
                                </div>
                            </Col>
                            <Col md={4}>
                            <div style={{ justifyContent: 'center', alignItems: 'center' }}>
                                    <ImCloud color='white' size={75} />
                                </div>
                            </Col>
                        </Row>
                    </Col>
                </Row>
                <BlueFooter/>
                </Card.Body>

        </Card>
           
    );
}

export default Home